interface option {
    name: "",
    value: "",
    bonusIndex: 0,
    type: 1,
    checked: false
};

export class PostBet { // 
    game_id: number;
    game_period: number;
    bet_next: number;
    amount: number;
    bet_more: number;
    bet_higher: number;
    perPeriodNum: number; //bet_higher
    bet_win_stop: number;
    bet_list: Bet_list[];
}

export class Bet_list {
    playId: number;
    subId: number;
    subName: string;
    betCode: string;
    betCount: number;
    betMoney: number;
    betEachMoney: number;
    betPrize: string;
    betPrizeShow: string;
    betReward: number;
    betPos: string; //0|1|2|3|4 
    betDbHighPrize: string; // 
    betDbLowPrize: string; // 
}

